@import '../../styles/shared/spacing.module.scss';

.gameHistory{
    width: 90vw;
    margin: auto;
    padding: 5px;

    &__game{
        margin-bottom: 70px;
        -webkit-box-shadow: 5px 2px 15px 5px rgba(36,112,107,1);
        -moz-box-shadow: 5px 2px 15px 5px rgba(36,112,107,1);
        box-shadow: 5px 2px 15px 5px rgba(36,112,107,1);
    }

    &__spinner{
        display: flex;
        margin: auto;
        padding-top: 50px;
        padding-bottom: 50px;
    }

}